// iconPosition /cfgTool
// backendUrl configuration tool, , /daac
// publishServer , , /daac
// openIFS 
export var config;
// truefalse Tomcat
(function(local){
  if (local) // 
		config = {
      iconPosition : "/cfgTool/assets/images/",
      backendUrl : "http://localhost:8080",
      publishServer: "http://localhost:8080",
      openIFS : false
    }
    else{ // tomcat
      config = {
        iconPosition : "/cfgTool/assets/images/",
        backendUrl : "http://localhost:8080/daac",
        publishServer: "http://localhost:8080/daac",
        openIFS : false
      }
    }
})(false);

